<template>
  <div id="app">
    <div v-if="prijavljen">
      <input type="Button" value="Одјави се"  v-on:click="logout()"/>
      <input type="Button" value="Профил" v-on:click="redirect('profil')"/><br/>

    </div>
    
    <div v-else>
      <input type="Button" value="Пријави се" v-on:click="redirect('login')"/>
      <input type="Button" value="Региструј се" v-on:click="redirect('register')"/>
    </div>
    <br/><br/><br/>
    <h1>Најновији тњитови</h1>
    <br/><br/>
    <div>
      
      <div class="ploce" v-for="p in podaci"><h5>{{p.KorisnickoIme}}</h5> <br/>
      &nbsp&nbsp&nbsp&nbsp {{p.Sadrzaj}}
      <br/><br/>
      ⬆️{{p.Goreglasovi}} ⬇️{{p.Doleglasovi}}&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
      {{p.Datum.slice(p.Datum.indexOf('T')+1,p.Datum.indexOf('.'))}} {{p.Datum.slice(0,p.Datum.indexOf('T'))}}
      <div v-if="prijavljen">
        <input type="button" value="Горегласај"/>
        <input type="button" value="Долегласај"/>
      </div>

      </div>
      
    </div>

  <router-view></router-view>
  </div>
</template>

<script>

export default{
data(){
  return{
    podaci:[]
  }

  },
  created(){
    fetch('https://vue-rest.herokuapp.com/get')
      .then(obj=>obj.json() )
        .then(res=>{
            this.podaci=res[0]
          })
          
  },

   mounted(){
    let korisnik=document.cookie.match("token")
    if(korisnik){
      this.$store.commit("prijavi");
    }
    else{
      this.$store.commit("odjavi");
    }
  },
  methods:{
    redirect(argument){
      this.$router.push({name:argument})
    },
    logout(){
      document.cookie = "token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
      document.cookie = "id=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
      this.$router.go();
    }
  },
  computed:{
    prijavljen(){
      return this.$store.state.prijavljen;
    }
  },
  sockets:{
    comment:function(data) {

        let podacii=JSON.parse(data);
        podacii=podacii[0];
        this.podaci.unshift(podacii[0]);
        },
    error(err){
    //  alert(err);
    }
  }
}
</script>

<style>

.ploce{
  border-top: 1px;
  border-left: 1px;
  border-right: 1px;
  border-bottom: 0px;
  border-style: solid;
  border-color: black;
  border-collapse: collapse;
}
.ishod{
  text-align: left;
}

</style>
